"use client";
import Image from "next/image";
import cloresal1 from "./../../public/images/cloresal1.png";
import cloresal2 from "./../../public/images/cloresal2.png";
import cloresal3 from "./../../public/images/cloresal3.png";
import { useRef, useEffect, useState } from "react";

const Cloresal = () => {
  const images = [
    cloresal1,
    cloresal2,
    cloresal3,
    cloresal1,
    cloresal2,
    cloresal3,
    cloresal1,
    cloresal2,
    cloresal3,
    cloresal1,
    cloresal2,
    cloresal3,
    cloresal1,
    cloresal2,
    cloresal3,
    cloresal1,
    cloresal2,
    cloresal3,
  ];

  const visibleImageCount = 4;
  const [startIndex, setStartIndex] = useState(0);

  const imagesToShow = images.slice(startIndex, startIndex + visibleImageCount);

  const containerRef = useRef(null);

  const scrollLeft = () => {
    setStartIndex(Math.max(0, startIndex - 1));
  };

  const scrollRight = () => {
    setStartIndex(Math.min(images.length - visibleImageCount, startIndex + 1));
  };

  return (
    <div className="relative">
      <button onClick={scrollLeft} disabled={startIndex === 0}>
        
      </button>
      
      <div
        ref={containerRef}
        className="flex overflow-x-auto scroll-smooth gap-4 py-8 px-4 min-w-full">
        {imagesToShow.map((image, index) => (
          <div
            key={index}
            className="relative w-1/4 min-h-[500px] border-4 border-gray-300 rounded-lg shadow-lg transition-transform transform hover:scale-[1.05]"
          >
            <Image
              src={image}
              alt={`cloresal-${index}`}
              height={500}
              width={500}
              objectFit="cover"
              className="absolute top-0 left-0 transition-opacity duration-300 hover:opacity-70"
            />

            <div className="absolute bottom-6 left-1/2 transform -translate-x-1/2 text-white text-center p-4 bg-gradient-to-t from-black to-transparent rounded-lg">
              <h2 className="text-xl font-semibold">
                Cloresal Movie {index + 1}
              </h2>
              <p className="mb-4 text-lg">
                Lorem ipsum dolor sit amet consectetur adipisicing elit.
                Aspernatur, est..
              </p>
              <button className="bg-blue-600 text-white py-2 px-6 rounded-lg hover:bg-blue-700 transition-colors duration-200">
                Watch Now
              </button>
            </div>
          </div>
        ))}
      </div>
      <button
        onClick={scrollRight}
        disabled={startIndex >= images.length - visibleImageCount}
      >
        &gt;
      </button>
    </div>
  );
};

export default Cloresal;
